.brand-container .react-resizable {
  position: relative;
  background-clip: padding-box;
}
.brand-container img {
  max-width: 100%;
}
.brand-container .react-resizable-handle {
  position: absolute;
  width: 10px;
  height: 100%;
  bottom: 0;
  right: -5px;
  cursor: col-resize;
  z-index: 1;
}
.brand-container td img {
  max-width: 90px;
}
.brand-container tr th {
  padding: 6px;
}
.brand-container tr td {
  padding: 6px;
}
.brand-info-model {
  text-align: left;
}
.brand-info-model .brand-form-image {
  padding-top: 20px;
}
.brand-info-model .brand-form-image img {
  max-width: 300px;
}
